<template>
  <div>
    <!-- 搜索页面头部 -->
    <div class="search-header">
      <!-- 后退按钮 -->
      <van-icon
        name="arrow-left"
        color="white"
        size="0.48rem"
        class="goback"
        @click="$router.back()"
      />
      <!-- 搜索组件 -->
      <van-search
        v-model="kw"
        v-fofo
        placeholder="请输入搜索关键词"
        background="#007BFF"
        shape="round"
        @input="inputFn"
        @search="searchTo()"
      />
    </div>

    <!-- 联想列表 -->
    <!-- 搜索建议列表 -->
    <div class="sugg-list" v-if="kw.length !== 0">
      <div
        class="sugg-item"
        v-for="(content, index) in suggestList"
        :key="index"
        v-html="lightFn(content, kw)"
        @click="suggestListTo(content)"
      ></div>
    </div>

    <!-- 搜索历史 -->
    <div class="search-history" v-else>
      <!-- 标题 -->
      <van-cell title="搜索历史">
        <!-- 使用 right-icon 插槽来自定义右侧图标 -->
        <template #right-icon>
          <van-icon name="delete" class="search-icon" @click="clearFn" />
        </template>
      </van-cell>

      <!-- 历史列表 -->
      <div class="history-list">
        <span
          class="history-item"
          v-for="(content, index) in history"
          :key="index"
          @click="historyTo(content)"
          >{{ content }}</span
        >
      </div>
    </div>
  </div>
</template>

<script>
import { suggestListAPI } from "@/api";
import { match } from "assert";
import { setStorage, getStorage } from "@/utils/storage";

export default {
  name: "Search",
  data() {
    return {
      kw: "", // 搜索关键字
      timer: null,
      suggestList: [], //联想列表
      history: JSON.parse(getStorage("his")) || [], // 搜索历史
    };
  },
  methods: {
    //搜索框内容变化时触发(
    inputFn() {
      //函数防抖
      clearTimeout(this.timer);
      //判断是否有搜索内容
      if (this.kw.trim().length === 0) {
        //清空联想列表
        this.suggestList = [];
      } else {
        this.timer = setTimeout(async () => {
          const res = await suggestListAPI(this.kw.trim());
          this.suggestList = res.data.data.options;
        }, 300);
      }
    },
    //关键词高亮显示
    lightFn(orgstr, kw) {
      if (!orgstr) {
        return;
      }
      //如果要使用变量作为匹配条件，则需要实例化对象
      const reg = new RegExp(kw, "ig"); //i忽略大小写，g匹配全局
      return orgstr.replace(reg, (match) => {
        //match为匹配的结果，不能用搜索内容替换，只能用匹配的结果替换
        return `<span style="color:red;">${match}</span>`;
      });
    },
    //跳转到搜索结果页面
    moveTo(kw) {
      //!坑,路由跳转在watch之前执行，所以用定时器包裹，让跳转在最后执行，这样watch里的方法才会执行
      setTimeout(() => {
        this.$router.push(`/search_result/${kw}`);
      }, 0);
    },
    searchTo() {
      //搜索内容为空则返回
      if (!this.kw.trim()) {
        return;
      }
      //保存到历史记录
      this.history.push(this.kw.trim());
      //搜索框回车事件
      this.moveTo(this.kw);
    },
    suggestListTo(kw) {
      //保存到历史记录
      this.history.push(kw);
      //联想列表点击事件
      this.moveTo(kw);
    },
    historyTo(kw) {
      //历史记录点击事件
      this.moveTo(kw);
    },
    //清空历史记录
    clearFn() {
      this.history = [];
    },
  },
  watch: {
    history: {
      handler() {
        //利用es6的新特性set给历史记录去重，
        const set = new Set(this.history);
        const newArr = Array.from(set);
        setStorage("his", JSON.stringify(newArr));
      },
    },
  },
};
</script>

<style scoped lang="less">
.search-header {
  height: 46px;
  display: flex;
  align-items: center;
  background-color: #007bff;
  overflow: hidden;
  /*后退按钮*/
  .goback {
    padding-left: 14px;
  }
  /*搜索组件*/
  .van-search {
    flex: 1;
  }
}

// 联想列表样式
/*搜索建议列表样式 */
.sugg-list {
  .sugg-item {
    padding: 0 15px;
    border-bottom: 1px solid #f8f8f8;
    font-size: 14px;
    line-height: 50px;
    // 实现省略号的三行代码
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}

/**搜索历史 */
.search-icon {
  font-size: 16px;
  line-height: inherit;
}

.history-list {
  padding: 0 10px;
  .history-item {
    display: inline-block;
    font-size: 12px;
    padding: 8px 14px;
    background-color: #efefef;
    margin: 10px 8px 0px 8px;
    border-radius: 10px;
  }
}
</style>
